import React, { useState } from "react";
import { motion } from "framer-motion";
import { pageAnimation, titleAnim, fade } from "../animation";
import styled from "styled-components";
import ig from "../img/ig.png";
import fb from "../img/fb.png";
import li from "../img/li.png";
import gh from "../img/gh.png";
import { useForm } from "react-hook-form";
import { init, sendForm } from "emailjs-com";
init(process.env.REACT_APP_INIT);
const ContactMe = () => {
  const { register, handleSubmit, watch, errors } = useForm();

  const [contactNumber, setContactNumber] = useState("000000");

  const generateContactNumber = () => {
    const numStr = "000000" + ((Math.random() * 1000000) | 0);
    setContactNumber(numStr.substring(numStr.length - 6));
  };

  const onSubmit = (data) => {
    generateContactNumber();
    const form = document.querySelector("#contact-form");
    sendForm("default_service", "template_n973evc", "#contact-form").then(
      function (response) {
        console.log("SUCCESS!", response.status, response.text);
        form.reset();
      },
      function (error) {
        console.log("FAILED...", error);
      }
    );
  };

  return (
    <ContactStyle
      exit="exit"
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      style={{ background: "#fff" }}
    >
      <Title>
        <motion.h2 variants={fade}>Get in touch.</motion.h2>
      </Title>
      <Social>
        <a href="https://www.linkedin.com/in/vishwas-modi-166981130/">
          <Circle>
            <img src={li} />
          </Circle>
        </a>

        <a href="https://www.instagram.com/modivishwas/">
          <Circle>
            <img src={ig} />
          </Circle>
        </a>

        <a href="https://www.instagram.com/modivishwas/">
          <Circle>
            <img src={fb} />
          </Circle>
        </a>

        <a href="https://github.com/vishwasmodi/">
          <Circle>
            <img src={gh} />
          </Circle>
        </a>
      </Social>

      <EmailForm>
        <form id="contact-form" onSubmit={handleSubmit(onSubmit)}>
          <input type="hidden" name="contact_number" value={contactNumber} />
          <div className="yourInfo">
            <input
              {...register("user_name")}
              type="text"
              name="user_name"
              placeholder="Your Name"
              id="name"
            />

            <input
              {...register("user_email")}
              type="email"
              placeholder="Your E-mail"
              name="user_email"
              id="email"
            />
          </div>
          <div className="yourTitle">
            <input
              {...register("user_title")}
              type="text"
              id="title"
              placeholder="Title of message"
              name="user_title"
              id="title"
            />
          </div>
          <div class="message">
            <textarea
              {...register("message")}
              placeholder="Message"
              name="message"
              id="msg"
            ></textarea>
          </div>
          <button type="submit">Submit</button>
        </form>
      </EmailForm>
    </ContactStyle>
  );
};

const ContactStyle = styled(motion.div)`
  padding: 5rem 7rem;
  color: #353535;
  min-height: 80vh;
`;

const Title = styled.div`
  margin-bottom: 4rem;
  color: black;
  @media (max-width: 1300px) {
    margin-bottom: 2rem;
  }
`;

const Hide = styled.div`
  overflow: hidden;
`;

const Circle = styled.div`
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  padding: 0rem 3rem;
  /* background: #353535; */
  img {
    height: 3.5rem;
    width: 3.5rem;
  }
  @media (max-width: 1300px) {
    height: 2rem;
    width: 2rem;
    img {
      height: 2rem;
      width: 2rem;
    }
  }
`;
const EmailForm = styled.div`
  display: flex;
  flex-direction: row;
  background-color: #353535;
  color: white;
  margin-top: 9rem;
  justify-content: center;
  padding: 10rem 0rem 0rem 0rem;

  input,
  textarea {
    all: unset;
    padding: 1.2rem;
    margin: 1.3rem;
    color: black;
    background: white;
  }
  button {
    padding: 1.3rem 2rem;
    margin-bottom: 1em;
    background: gray;
    color: white;
    border: 0;
  }
  #msg {
    width: 85%;
    min-height: 20rem;
  }
  #title {
    display: flex;
    width: 85%;
  }
`;

const Social = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: center;
  @media (max-width: 1300px) {
    padding: 2rem 2rem;
  }
`;
export default ContactMe;
